html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.section-inner {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 1180px;
  height: 100vh;
  margin: 0 auto;
  z-index: 10; }

.section-header {
  position: relative;
  color: #ffffff;
  background-color: #000000;
  background: url("../image/photo-of-people-using-laptop-s-3182759.jpg") no-repeat 50% 50%;
  background-size: cover; }
  .section-header::before {
    content: '';
    position: absolute;
    z-index: 5;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(20, 39, 41, 0.7); }
  .section-header-logo {
    padding-top: 35px;
    flex-shrink: 1;
    font-weight: bold;
    font-size: 24px; }
  .section-header-content {
    flex-grow: 1;
    display: flex;
    align-items: center; }
  .section-header-wrapper {
    margin: 0 auto; }
    .section-header-wrapper_title {
      margin-bottom: 16px;
      font-weight: bold;
      font-size: 56px;
      line-height: 60px;
      text-align: center; }
    .section-header-wrapper_subtitle {
      font-family: 'Montserrat', sans-serif;
      width: 70%;
      margin: 0 auto 44px;
      font-size: 18px;
      line-height: 32px;
      text-align: center; }
    .section-header-wrapper_form {
      display: flex; }
      .section-header-wrapper_form-input {
        padding-right: 40px; }
        .section-header-wrapper_form-input input {
          font-family: 'Montserrat', sans-serif;
          margin: 0;
          padding: 16px 20px;
          flex-grow: 1;
          width: 100%;
          font-size: 16px;
          color: #ffffff;
          background-color: rgba(0, 0, 0, 0.3);
          border: none;
          border-radius: 4px; }
          .section-header-wrapper_form-input input::placeholder {
            color: #ffffff; }
      .section-header-wrapper_form-button button {
        font-family: 'Montserrat', sans-serif;
        margin: 0;
        padding: 16px 20px;
        font-weight: bold;
        font-size: 16px;
        color: #ffffff;
        background: linear-gradient(to right, #fe9858, #fe4fa7);
        border: none;
        border-radius: 4px; }

.section-promo .section-promo-1_video img.video {
  background: url("../image/photo-of-people-using-laptop-s-3182759.jpg") no-repeat 50% 50%;
  background-size: 50px 50px; }
